ght: 55vh;\n}\n\n.resume .download-content {\n  margin: 0;\n  position: absolute;\n  top: 44%;\n  transform: translateY(-50%);\n}\n\n@media screen and (max-width: 640px) {\n  .resume .download-content a {\n    font-size: 20px;\n  }\n}\n\n.resume .bg source,\n.resume .bg img {\n  opacity: 0.5;\n}\n\n@media screen and (min-width: 641px) and (max-width: 768px) {\n  span.number {\n    width: 15vw;\n    height: 15vw;\n    border-radius: 15vw;\n    font-size: 10vw;\n    line-height: 13vw;\n    border: solid 1vw !important;\n  }\n\n  .container .text-container,\n.container .button-container {\n    display: block !important;\n  }\n  .container .text-container .column,\n.container .button-container .column {\n    width: 45% !important;\n    float: left !important;\n  }\n  .container .text-container .column:nth-child(odd),\n.container .button-container .column:nth-child(odd) {\n    margin-right: 10%;\n  }\n  .container .text-container .column p,\n.container .button-container .column p {\n    padding: 25px 0;\n  }\n\n  .section-3 .container .column.col-2, .section-3 .container .column.col-3 {\n    width: 50% !important;\n    float: left !important;\n  }\n  .section-3 .container .column.col-2 .img-container, .section-3 .container .column.col-3 .img-container {\n    margin-left: 0 !important;\n    margin-bottom: 40px;\n    float: right;\n    width: 90%;\n    padding-bottom: 130%;\n  }\n  .section-3 .container .column.col-5 {\n    width: 100% !important;\n  }\n  .section-3 .container .column.col-5 .x-border {\n    margin-left: 0%;\n  }\n}\n\n@media screen and (max-width: 640px) {\n  .subheading .down_arrow {\n    bottom: -105px;\n  }\n\n  .description .container .text-container {\n    display: grid;\n  }\n  .description .container .text-container .column {\n    width: 100% !important;\n    float: left !important;\n  }\n  .description .container .text-container .column p {\n    padding: 25px 0;\n  }\n  .description .container .button-container {\n    display: block;\n  }\n  .descr